/**
 * 
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
export function IDcard(rule, value, callback) {
  if (value) {
    //\!\@\#\$\%\^\&\*
    var reg = /^[A-Za-z0-9\(\)]+$/;
    if (reg.test(value) == false) {
      callback(new Error(""));
    } else {
      callback();
    }
  } else {
    callback();
  }
}

/**
 * 
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
export function mobile(rule, value, callback) {
  if (value) {
    var reg = /^1[3,4,5,6,7,8,9][0-9]\d{8}$/;
    if (reg.test(value) == false) {
      callback("");
    } else {
      callback();
    }
  } else {
    callback();
  }
}

/**
 * 
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
export function mobileOrphone(rule, value, callback) {
  if (value) {
    var reg = /^1[3,4,5,6,7,8,9][0-9]\d{8}$/;
    var reg1 = /^[0][1-9]{2,3}-[0-9]{5,10}$/; //
    if (reg.test(value) == false && reg1.test(value) == false) {
      callback("");
    } else {
      callback();
    }
  } else {
    callback();
  }
}

