/**
 *  0-100
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
export function positivenumberpoint_0_100(rule, value, callback) {
  if (value) {
    var reg = /^\d+(\.\d+)?$/;
    if (reg.test(value) == false) {
      callback("");
    } else if (value > 100 || value < 0) {
      callback("0-100");
    } else {
      callback();
    }
  } else {
    callback();
  }
}

