/**
 * Number formattingK,M,G,T,P,E
 * like 10000 => 10k
 * @param {number} num 
 * @param {number} digits 
 */
export function numberFormatter(num, digits) {
  const si = [{
      value: 1E18,
      symbol: 'E'
    },
    {
      value: 1E15,
      symbol: 'P'
    },
    {
      value: 1E12,
      symbol: 'T'
    },
    {
      value: 1E9,
      symbol: 'G'
    },
    {
      value: 1E6,
      symbol: 'M'
    },
    {
      value: 1E3,
      symbol: 'k'
    }
  ]
  for (let i = 0; i < si.length; i++) {
    if (num >= si[i].value) {
      return (num / si[i].value).toFixed(digits).replace(/\.0+$|(\.[0-9]*[1-9])0+$/, '$1') + si[i].symbol
    }
  }
  return num.toString()
}


/**
 * 
 * @returns 
 */
export function guid() {
  var s = [];
  var hexDigits = "0123456789abcdef";
  for (var i = 0; i < 36; i++) {
    s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
  }
  s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = "-";

  var uuid = s.join("");
  return uuid;
}


/**
 * *
 * @param {string} str 
 * @param {number} s 
 * @param {number} e 
 * @returns 
 */
export function starReplace(str, s, e) {
  if (!str) return str;

  var _k = !!e && e != 0 ? str.substring(s, e) : str.substring(s);

  var _v = "";

  for (var i = 0; i < _k.length; i++) _v += "*";
  return str.replace(_k, _v);
}

/**
 * 
 * @param {String} money 
 * @returns 
 */
export function convertCurrency(money) {
  //
  var cnNums = new Array('', '', '', '', '', '', '', '', '', '');
  //
  var cnIntRadice = new Array('', '', '', '');
  //
  var cnIntUnits = new Array('', '', '', '');
  //
  var cnDecUnits = new Array('', '', '', '');
  //
  var cnInteger = '';
  // var cnInteger = '';
  //
  var cnIntLast = '';
  //
  var maxNum = 999999999999999.9999;
  //
  var integerNum;
  //
  var decimalNum;
  //
  var chineseStr = '';
  //
  var parts;
  if (money === '' || money === null || money === undefined) {
    return '';
  }
  money = parseFloat(money);
  if (money >= maxNum) {
    //
    return '';
  }
  if (money == 0) {
    chineseStr = cnNums[0] + cnIntLast + cnInteger;
    return chineseStr;
  }
  //
  money = money.toString();
  if (money.indexOf('.') == -1) {
    integerNum = money;
    decimalNum = '';
  } else {
    parts = money.split('.');
    integerNum = parts[0];
    decimalNum = parts[1].substr(0, 4);
  }
  //
  if (parseInt(integerNum, 10) > 0) {
    var zeroCount = 0;
    var IntLen = integerNum.length;
    for (var i = 0; i < IntLen; i++) {
      var n = integerNum.substr(i, 1);
      var p = IntLen - i - 1;
      var q = p / 4;
      var m = p % 4;
      if (n == '0') {
        zeroCount++;
      } else {
        if (zeroCount > 0) {
          chineseStr += cnNums[0];
        }
        //
        zeroCount = 0;
        chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
      }
      if (m == 0 && zeroCount < 4) {
        chineseStr += cnIntUnits[q];
      }
    }
    chineseStr += cnIntLast;
  }
  //
  if (decimalNum != '') {
    var decLen = decimalNum.length;
    for (var i = 0; i < decLen; i++) {
      var n = decimalNum.substr(i, 1);
      if (n != '0') {
        chineseStr += cnNums[Number(n)] + cnDecUnits[i];
      } else {
        chineseStr += cnNums[Number(n)] + cnDecUnits[i];
      }
    }
  }
  if (chineseStr == '') {
    chineseStr += cnNums[0] + cnIntLast + cnInteger;
  } else if (decimalNum == '') {
    chineseStr += cnInteger;
  }
  return chineseStr;
}



/**
 * 
 * @param {string} str 
 * @returns 
 */
export function type(data) {
  let res = Object.prototype.toString.call(data).slice(8, -1)
  if (res == 'String') {
    return "str"
  } else if (res == 'Number') {
    return "num"
  } else if (res == 'Boolean') {
    return "boo"
  }
  // else if (res == 'Null'){
  // 	return "null"
  // }
  else if (res == 'Undefined' || res == 'Null') {
    return "und"
  } else if (res == 'Object') {
    return "obj"
  } else if (res == 'Function') {
    return "fn"
  } else if (res == 'Date') {
    return "date"
  } else if (res == 'RegExp') {
    return "reg"
  } else if (res == 'Array') {
    return "arr"
  }
}
